export default function Card({projectName, desc, links}){
      return(
        <>
            <div className="flex flex-row flex-wrap justify-center pt-40">
                <div className="bg-maroon mx-12 my-12 w-72 h-auto flex flex-col justify-between">
                    <div className="py-3 px-5">
                    <h1 className="text-3xl pb-3">
                        { projectName }          
                    </h1>
                    <h2 className="pb-3 text-xl">
                        { desc }      
                    </h2>
                </div>
                <div className="px-5 py-3">
                        {links.map(obj => (
                            <a target="_blank" href={obj.link}><i class={setClass(obj.type)}></i></a>          
                        ))}     
                    </div>
                </div>
            </div>
        </>
      )
  }

function setClass(icon) {
    if (icon != "book") {
        return "fab fa-" + icon + " fa-2x mr-2";
    } else {
        return "fas fa-" + icon + " fa-2x mr-2";
    }

}